.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

#typeDropdown {
  margin-bottom: 7.5em;
}

ul {
  list-style-type: none;
  padding-left: 0;
}





.grid-container {
  display: grid;
  grid-template-columns: auto auto auto auto;
  gap: 1em;
  padding: 1em;
}



.image {
  width: 200px;
  height: auto;
  margin-bottom: 0.5em;
  border-radius: 4px;
}